import { FETCH_GRAPH_DATA, CREATE_NODE, UPDATE_NODE, DELETE_NODE } from './constants';

const initialState = {
  fetching: {
    selectedGraph: false,
    createNode: false,
    updateNode: false,
    deleteNode: false,
  },
  selectedGraph: null,
  orphans: null,
  displayedGraph: null,
};

const workspaceReducer = (state = initialState, action = {}) => {
  switch (action.type) {
    case `${FETCH_GRAPH_DATA}_PENDING`:
      return {
        ...state,
        fetching: { ...state.fetching, selectedGraph: true },
      };
    case `${FETCH_GRAPH_DATA}_FULFILLED`: {
      const { orphans, ...graphData } = action.payload;
      const { nodes } = graphData;
      const selectedGraph = {
        ...graphData,
        nodes: nodes.map(node => ({ ...node, name: node.properties.name })),
        focusedNodeId: '',
      };
      return {
        ...state,
        selectedGraph,
        orphans: orphans.map(orphan => ({ ...orphan, name: orphan.properties.name })),
        fetching: { ...state.fetching, selectedGraph: false },
      };
    }
    case `${FETCH_GRAPH_DATA}_REJECTED`:
      return {
        ...state,
        fetching: { ...state.fetching, selectedGraph: false },
      };
    case `${CREATE_NODE}_PENDING`:
      return {
        ...state,
        fetching: {
          ...state.fetching,
          createNode: true,
        },
      };
    case `${CREATE_NODE}_FULFILLED`:
      return {
        ...state,
        fetching: {
          ...state.fetching,
          createNode: false,
        },
      };
    case `${CREATE_NODE}_REJECTED`:
      return {
        ...state,
        fetching: {
          ...state.fetching,
          createNode: false,
        },
      };
    case `${UPDATE_NODE}_PENDING`:
      return {
        ...state,
        fetching: { ...state.fetching, updateNode: true },
      };
    case `${UPDATE_NODE}_FULFILLED`:
      return {
        ...state,
        fetching: { ...state.fetching, updateNode: false },
      };
    case `${UPDATE_NODE}_REJECTED`:
      return {
        ...state,
        fetching: { ...state.fetching, updateNode: false },
      };
    case `${DELETE_NODE}_PENDING`:
      return {
        ...state,
        fetching: { ...state.fetching, deleteNode: true },
      };
    case `${DELETE_NODE}_FULFILLED`:
      return {
        ...state,
        fetching: { ...state.fetching, deleteNode: false },
      };
    case `${DELETE_NODE}_REJECTED`:
      return {
        ...state,
        fetching: { ...state.fetching, deleteNode: false },
      };
    default:
      return state;
  }
};

export default workspaceReducer;
